body {
  font-size: 10px;
  font-family: Tahoma, Arial;
}
div.menu {
  width: 100%;
  background-color: #4d5791;
  height: 44px;
  padding: 0 40px;
}
div.hero {
  height: 350px;
  background-color: #b4c6f0;
  background-image: url('bg.png');
  background-size: 400px;
  background-repeat: no-repeat;
  background-position: center bottom;
  padding-top: 40px;
}
div.hero h1,
div.hero h2 {
  text-align: center;
  margin: 0;
}
div.hero h1 {
  color: #fff;
  font-size: 4.2em;
  margin-bottom: 20px;
}
div.hero h2 {
  color: #4d5791;
}
.container {
  padding: 40px;
  width: 100%;
}
.page-area {
  max-width: 768px;
  display: inline-block;
}
.bottom {
  height: 50px;
  background-color: #b4c6f0;
  padding: 40px;
}
.center {
  text-align: center;
}
footer {
  height: 50px;
  background-color: #b4c6f0;
  padding: 40px;
  text-align: center;
}
.container pb-article {
  display: inline-block;
  width: 100%;
  max-width: 340px;
  text-align: justify;
  margin: 20px;
}
@media screen and (min-width: 768px) {
  .container article {
    margin: 10px;
  }
}
a#skip, a#skip:hover, a#skip:visited {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  top: -500px;
}
a#skip:focus {
  position: static;
  width: auto;
  height: auto;
}
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: relative;
  height: 200px;
}
svg {
  width: 100%;
  height: 100%;
}
path.slice {
  stroke-width: 2px;
}
polyline {
  opacity: 0.3;
  stroke: black;
  stroke-width: 2px;
  fill: none;
}
#myChart {
  width: 200px;
  height: 200px;
}